// Nom du point dâarrÃªt suivant, ou null pour le dernier point dâarrÃªt.
@function breakpoint-next($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {
  $n: index($breakpoint-names, $name);
  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);
}

// Largeur minimale de point dâarrÃªt. Null pour les plus petits (premier) point dâarrÃªt.
//
//    >> breakpoint-min(s, (s: 0, m: 768px))
//    544px
@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {
  $min: map-get($breakpoints, $name);
  @return if($min != 0, $min, null);
}

// La valeur maximale est calculÃ©e comme le minimum de la prochaine moins 0,1.
//
//    >> breakpoint-max(s, (s: 0, m: 768px))
//    767px
@function breakpoint-max($name, $breakpoints: $grid-breakpoints) {
  $next: breakpoint-next($name, $breakpoints);
  @return if($next, breakpoint-min($next, $breakpoints) - 1px, null);
}

// MÃ©dias dâau moins la largeur minimale de point dâarrÃªt. Aucune requÃªte pour le plus petit point dâarrÃªt.
// En fait le @content sâapplique sur le point dâarrÃªt donnÃ© et plus large.
@mixin media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

// MÃ©dias dâau maximum la largeur maximale de point dâarrÃªt. Aucune requÃªte pour le plus grand point dâarrÃªt.
// En fait le @content sâapplique sur le point dâarrÃªt donnÃ© et plus Ã©troit.
@mixin media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {
  $max: breakpoint-max($name, $breakpoints);
  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}

// Les mÃ©dias entre la largeurs minimum et maximum du point d'arrÃªt.
// Pas de minimum pour le plus petit point dâarrÃªt et pas de maximum pour le plus grand.
// En fait le @content sâapplique uniquement au point dâarrÃªt donnÃ©, pas de Viewports tout plus larges ou plus Ã©troits.
@mixin media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {
  @include media-breakpoint-up($name, $breakpoints) {
    @include media-breakpoint-down($name, $breakpoints) {
      @content;
    }
  }
}

// Les mÃ©dias qui sâÃ©tend sur plusieurs largeurs de point dâarrÃªt.
// En fait le @content s'applique entre le min et max points dâarrÃªt
@mixin media-breakpoint-between($lower, $upper, $breakpoints: $grid-breakpoints) {
  @include media-breakpoint-up($lower, $breakpoints) {
    @include media-breakpoint-down($upper, $breakpoints) {
      @content;
    }
  }
}
